.app__nav{width:100%}.app-nav__link{width:100%;display:flex;align-items:center;justify-content:flex-start;text-align:left;padding-left:20px;font-size:18px;color:#665;text-decoration:none;background-color:#111;border-left:1px dotted #333;box-shadow:0 0 5px #000 inset;transition:all 150ms;font-family:"BebasNeueRegular",Arial,sans-serif}@media screen and (min-width: 630px){.app-nav__link{justify-content:center;font-size:30px;line-height:65px;height:65px;width:25%;text-align:center;padding-left:0}}.app-nav__link:hover{color:#ddd}.app-nav__link:hover .nav__glyph{color:#eabb00}.app-nav__link:hover .cog{transform:rotate(60deg)}.app-nav__link:hover .share{transform:rotate(-45deg)}.app-nav__link:hover .mail{transform:scale(1.2)}.app-nav__link:hover .document_alt_fill{transform:scale(1.2)}.app-nav__link.active{color:#eabb00;cursor:default;background-color:#222;text-shadow:0 1px 3px #000;box-shadow:-8px 0 18px rgba(0,0,0,.7) inset,8px 0 18px rgba(0,0,0,.7) inset}.app-nav__link.active:hover{color:#eabb00}.app-nav__link.active:hover .nav__glyph{color:#00fffb}.app-nav__link.active .nav__glyph{color:#00fffb}.nav__glyph{display:inline-block;font-family:"IconicStroke";color:#3a3a3a;margin-right:15px;font-size:18px;position:relative;transition:all 150ms}@media screen and (min-width: 630px){.nav__glyph{font-size:30px}}.app-nav__trigger{z-index:5;width:40px;height:40px;cursor:pointer;text-align:center;position:absolute;top:15px;right:20px;border-radius:3px;border:1px solid #353535;background:rgba(0,0,0,.4)}.app-nav__trigger:hover .nav__glyph{color:#ccc}.app-nav__trigger .nav__glyph{margin:0 !important;color:#888;font-size:27px}.app-nav__trigger--is-active .nav__glyph{color:#ccc}@media screen and (min-width: 630px){.app-nav__trigger{display:none}}.app-nav__wrapper{display:flex;position:absolute;flex-wrap:wrap;width:100%;max-width:1000px;z-index:1;transition:transform .3s;background-color:#000;box-shadow:0 1px 5px #000;border:2px solid #333;transform:translateX(100%)}.app-nav__wrapper--is-open{transform:translateX(0)}@media screen and (min-width: 1000px){.app-nav__wrapper{left:calc((100vw - 1000px)/2)}}@media screen and (min-width: 630px){.app-nav__wrapper{transform:translateX(0)}}.mail:before{content:"✉"}.document_alt_fill:before{content:""}.cog:before{content:"⚙"}.share:before{content:""}.layers:before{content:""}